.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 170px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    background: #5A5B57;

    img {
        width: 80%;
        max-width: 700px;
        margin: 9px auto 0 auto;
    }

    
}